import { Component } from '@angular/core';
import { ActionSheetController  ,AlertController, IonicPage,ModalController, NavController, NavParams, InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-categoriaformsusr-list',
  templateUrl: 'categoriaformsusr-list.html',
})
export class CategoriaformsusrListPage {

  public loading: boolean = true;
  items: any=[];
  items2: any=[];
    tiposus: any=[];
    generated = '';
    public hasFilter: boolean = false;
   idcat:any;
   idcatn:any;
   idempresa:any;
 
   idafiliado:any='na';
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";
//private barcodeScanner:BarcodeScanner,
  constructor(public actionSheetCtrl: ActionSheetController,public alertCtrl: AlertController,public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService
    ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth
   
    ) {

      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value
        
        
    })
  }


  
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }

  
  
  pideregistros() { 
      let self = this;
      self.loading = true;
      this.regService.getReg(this.navParams.data.record._id +'/categoria/' + this.idempresa,'catusuarios').then((data) => {
        this.items=data;
        this.items2=data;
        self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
   // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })  
  });
  }
  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }
  ionViewDidLoad() {

    this.idcat=this.navParams.data.record._id;
    this.idcatn=this.navParams.data.record.nombre;

    this.pideregistros();
  }
  
  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }


  deleteRecord(item : any, no) : void
  {

    let alert = this.alertCtrl.create({ title: 'Esta Seguro de realizar operación?',
      message: 'Operación eliminacion de registro',
    buttons: [
      {text: 'Si',
        handler: () => {
              
          let recordID 		: string		= item._id;
          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'catusuarios').then((data) => {
            this.pideregistros();
            }, (err) => {
              this.msj.showError3('Error',err.error);
          });
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();


    
  
  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('CategoriaformsusrCreatePage', { record : item ,record2:{idcategoria:this.idcat}},{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
       this.pideregistros();
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void
   {// backdropDismiss: false
    let addModal = this._MODAL .create('CategoriaformsusrCreatePage',{ record : null,record2:{idcategoria:this.idcat} },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros();
      }  
    })
    addModal.present();

      //this.navCtrl.push('EmpresaCreatePage');
   }
 
   conferenciaRecord(item : any) : void
   {
   
    //  this.navCtrl.push('UnidadacademicaListPage', { record : item });
   }


   showFilter()     
   {
 
     let self = this;


             self.loading = false;
             this.hasFilter = true;
                         let select = {
                           title: 'Seleccione categoria',
                           message: 'Categorias',    inputs: [],
                           buttons: [           {            text: 'Cancelar'     },    {       text: 'Buscar',handler: todo => { 
                               //buscamos
                               this.items=this.items2
                               this.items= this.items2.filter((v) => {
                                 if(v.idformulario ) {
                                   
                                   if (v.idformulario._id==todo) {
                                     return true;
                                   }
                                   return false;
                                   
                                 }
                               });   
                             }  } ]
                       }
                       this.regService.getReg('todos/' + this.idempresa,'frmmovils').then((data) => {
                         var aa:any;
                         aa=data;
                         for (var i = 0; i < aa.length; i++) {
                           select.inputs.push({name: 'myInput', type: 'radio',label:aa[i].nombre,value:aa[i]._id});
                       }
                       let newAlert = this.alertCtrl.create(select);
                       newAlert.present();
                   }, (err) => {
                     this.msj.showError3('Error',err.error);
                   });
         
   
 
     
 
 
       
 
 
   }
  

}



